@include body-class(true) {
.wpmud-analytics-table-change {
	text-align: center;
}
.wpmud-analytics-table-change i {
	margin-right: 0px!important;
}
.wpmud-analytics-table-change.wpmud-analytics-table-up,
.wpmud-analytics-table-change.wpmud-analytics-table-up i:before {
	color: rgb(38, 191, 161);
}
.wpmud-analytics-table-change.wpmud-analytics-table-down,
.wpmud-analytics-table-change.wpmud-analytics-table-down i:before {
	color: red;
}
.wpmud-analytics-table-total {
	text-align: right;
}
.wpmud-whitelabel-table-status span {
	background: #e6e6e6;
}
.wpmud-whitelabel-table-status.wpmud-whitelabel-table-status-on span {
	background: #17a8e3;
}
}
